import { BuildContext, Component, HtmlElement, StatelessComponent, StyleBuilder } from "@equantic/runtime";
import { Dictionary } from "./Dictionary";
import { DynamicElement } from "./DynamicElement";
import { Text } from "./Text";

export class Button extends StatelessComponent {
    build(context: BuildContext) {
        let theme = context.getService();let buttonTheme = theme?.button;let attrs = { ["type"]: this.type, ["class"]: StyleBuilder.create(buttonTheme?.base).add(buttonTheme?.getVariant(this.variant)).add(buttonTheme?.getSize(this.size)).add(this.className).build() };if (this.disabled) attrs['disabled'] = 'true';let element = new DynamicElement({ tagName: 'button', customAttributes: attrs, customEvents: this.buildEvents() });if ((this.children.length > 0)) {for (const child of this.children) {element.children.push(child);}} else if (this.text != null) {element.children.push(new Text(this.text));}return element;
    }

}

