        if (!this.isOpen) return new NullComponent();let sideClass = this.side === 'right' ? 'right-0 border-l' : 'left-0 border-r';let slideAnim = this.side === 'right' ? 'slide-in-from-right' : 'slide-in-from-left';return new Box({ className: 'fixed inset-0 z-50 z-[100]', children: [new Box({ className: 'absolute inset-0 bg-black/20 backdrop-blur-sm animate-in fade-in duration-300', onClick: () => this.onClose?.invoke() }), new Box({ className: `absolute inset-y-0 ${sideClass} ${this.width} bg-white dark:bg-zinc-900 shadow-2xl animate-in ${slideAnim} duration-300 border-gray-100 dark:border-zinc-800 flex flex-col`, children: [new Row({ className: 'p-4 justify-end', children: [new Button({ text: '✕', variant: 'ghost', className: 'h-10 w-10 p-0 rounded-full text-gray-400 hover:text-gray-900 dark:hover:text-white hover:bg-gray-100 dark:hover:bg-zinc-800 relative z-50', onClick: () => this.onClose?.invoke() })] }), new Box({ className: 'flex-1 overflow-y-auto p-4', children: [this.content ?? new NullComponent()] })] })] });
