        let theme = context.getService();let cardTheme = theme != null ? theme.card : null;let shadowKey = (String(this.shadow) ?? 'medium').toLowerCase();let shadowClass = '';if (cardTheme != null && cardTheme.shadows != null) {let s = cardTheme.shadows[shadowKey];if (s != null) shadowClass = s;}let containerClass = '';if (cardTheme != null) containerClass = cardTheme.container;let headerClass = '';if (cardTheme != null) headerClass = cardTheme.header;let bodyClass = '';if (cardTheme != null) bodyClass = cardTheme.body;let footerClass = '';if (cardTheme != null) footerClass = cardTheme.footer;let classes = [];if (!!containerClass) classes.push(containerClass);if (!!shadowClass) classes.push(shadowClass);if (!!this.width) classes.push(this.width);if (!!this.className) classes.push(this.className);let cardContainer = new Box({ className: classes.join(' '), children: [] });if (this.header != null) {cardContainer.children.push(new Box({ className: headerClass, children: [this.header] }));}let textBody = new Text('');if (this.body != null) {cardContainer.children.push(new Box({ className: bodyClass, children: [this.body] }));} else {cardContainer.children.push(new Box({ className: bodyClass, children: [textBody] }));}if (this.footer != null) {cardContainer.children.push(new Box({ className: footerClass, children: [this.footer] }));}return cardContainer;
